@charset "UTF-8";
@font-face {
  font-family: "PT Sans Narrow";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/pt-sans-narrow-regular.woff2") format("woff2"), url("../../fonts/pt-sans-narrow-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans Narrow";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/pt-sans-narrow-bold.woff2") format("woff2"), url("../../fonts/pt-sans-narrow-bold.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
  font-size: 18px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  line-height: 27px;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #f8f5f2;
  color: #000000;
}

.page-header {
  display: flex;
  flex-direction: column;
  background-color: #242424;
  color: #ffffff;
}

.page-header__logo {
  height: 85px;
  text-align: center;
}
@media (min-width: 768px) {
  .page-header__logo {
    order: 2;
    height: 400px;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .page-header__logo {
    background-color: #000000;
    background-image: url("../../image/bg-header.jpg");
    background-repeat: no-repeat;
    background-position: 50% -9px;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .page-header__logo {
    height: 648px;
    background-position: 50% 0;
    background-size: cover;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    height: 651px;
  }
}

.page-header__logo img {
  width: 226px;
  height: 85px;
}
@media (min-width: 1200px) {
  .page-header__logo img {
    margin-top: 57px;
  }
}

.main-nav {
  position: relative;
  width: auto;
  min-height: 51px;
  background-color: #826550;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #000000;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: #6a4d38;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover, .main-nav__toggle:active {
  background-color: #624530;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  top: 15px;
  left: 17px;
  content: "";
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .site-list__item--active {
    display: block;
  }

  .main-nav--closed .site-list__item--active a {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }

  .main-nav--closed .user-list__item {
    display: none;
  }
}
.main-nav--opened .main-nav__wrapper {
  position: absolute;
  top: 0;
  right: 0;
  left: 70px;
  z-index: 10;
  min-height: 80vh;
  background-color: #6a4d38;
}
.main-nav--opened .main-nav__wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 8px;
  box-shadow: inset 4px 0 7px -2px rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}

.main-nav--nojs .site-list__item {
  display: block;
}

.main-nav--nojs .site-list__item--active a {
  text-align: left;
  padding-left: 21px;
  padding-right: 21px;
}

.main-nav--nojs .user-list__item {
  display: block;
  background-color: #6f4d38;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  left: 0;
  right: auto;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  position: absolute;
  content: "";
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  border-radius: 2px;
  top: 24px;
  left: 17px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotateZ(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}

@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 84%;
    margin: 0 auto;
    /* 960px / .84 ~= 1142px (центровка начнётся чуть раньше 1200px) */
    max-width: 960px;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  outline: 5px solid rgba(0, 128, 0, 0.5);
  outline-offset: -5px;
}

.main-nav__list a {
  color: #ffffff;
}

.site-list {
  font-weight: bold;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 570px;
    flex-grow: 1;
  }
}
@media (min-width: 1200px) {
  .site-list {
    width: 780px;
  }
}

@media (min-width: 768px) {
  .site-list__item {
    margin-right: 32px;
  }
  .site-list__item:last-child {
    margin-right: 0;
  }
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    height: 2px;
    background-color: #826550;
  }
}
@media (min-width: 1200px) {
  .site-list__item {
    margin-right: 0;
  }
  .site-list__item--active::after {
    right: 30px;
    left: 30px;
  }
}

@media (min-width: 768px) {
  .site-list__item a[href]:hover {
    color: #856550;
    background-color: #000000;
  }
  .site-list__item a[href]:active {
    color: #ffffff;
    background-color: #000000;
    opacity: 0.3;
  }
}
@media (min-width: 1200px) {
  .site-list__item a[href]:hover {
    color: #000000;
    background-color: #ffffff;
  }
  .site-list__item a[href]:active {
    color: rbga(0, 0, 0, 0.3);
    background-color: #ffffff;
    opacity: 1;
  }
}

.site-list a {
  position: relative;
  display: block;
  padding: 12px 21px;
  padding-bottom: 13px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  line-height: 24px;
  /* 133.333% */
  background-color: #826550;
  border-bottom: 1px solid #71543f;
  border-top: 1px solid #886e5b;
}
@media (min-width: 768px) {
  .site-list a {
    padding: 0;
    padding-top: 25px;
    padding-bottom: 23px;
    border: none;
    background: none;
  }
}
@media (min-width: 1200px) {
  .site-list a {
    padding-right: 31px;
    padding-left: 31px;
  }
}

@media (min-width: 768px) {
  .user-list {
    width: 60px;
  }
}
@media (min-width: 1200px) {
  .user-list {
    width: auto;
    min-width: 150px;
  }
}

.user-list__login {
  position: relative;
  display: block;
  color: #ffffff;
  line-height: 18px;
  /* 133.333% */
  padding: 17px 21px;
  padding-left: 47px;
  text-decoration: none;
}
@media (min-width: 768px) {
  .user-list__login {
    width: 60px;
    height: 70px;
    padding: 0;
  }
  .user-list__login span {
    position: absolute;
    opacity: 0;
  }
  .user-list__login:hover .user-list__login-icon {
    opacity: 1;
  }
  .user-list__login:hover .user-list__login-icon polygon {
    fill: #826550;
  }
  .user-list__login:active .user-list__login-icon {
    opacity: 0.3;
  }
  .user-list__login:active .user-list__login-icon polygon {
    fill: #ffffff;
  }
}
@media (min-width: 1200px) {
  .user-list__login {
    position: relative;
    width: auto;
    height: auto;
    padding-top: 25px;
    padding-right: 30px;
    padding-bottom: 23px;
    padding-left: 50px;
    font-size: 18px;
    line-height: 24px;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
  }
  .user-list__login span {
    position: static;
    opacity: 1;
  }
  .user-list__login:hover {
    color: #000000;
    background-color: #ffffff;
  }
  .user-list__login:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
  }
  .user-list__login:hover .user-list__login-icon {
    opacity: 0.3;
  }
  .user-list__login:hover .user-list__login-icon polygon {
    fill: #000000;
  }
  .user-list__login:active .user-list__login-icon {
    opacity: 0.1;
  }
}

.user-list__login-icon {
  position: absolute;
  line-height: 18px;
  top: 17px;
  left: 21px;
  width: 16px;
  height: 16px;
  opacity: 0.3;
  fill: #ffffff;
}
@media (min-width: 768px) {
  .user-list__login-icon {
    top: 29px;
    left: 12px;
  }
}
@media (min-width: 1200px) {
  .user-list__login-icon {
    left: 15px;
  }
}

.stats {
  background-color: #292929;
  color: #ffffff;
}

@media (min-width: 768px) {
  .stats__wrapper {
    padding: 0 64px;
  }
}
@media (min-width: 1200px) {
  .stats__wrapper {
    margin: auto;
    max-width: 960px;
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
}

.stats__header {
  padding: 28px 20px;
  background-color: #ffffff;
  color: #000000;
}

.stats__slogan {
  margin: 0;
  margin-bottom: 13px;
  color: #000;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
  line-height: 1;
  /* 100% */
  text-transform: uppercase;
}

.stats__intro {
  margin: 0;
  margin-right: 20px;
  margin-left: 20px;
  color: #000;
  text-align: center;
  font-size: 20px;
  line-height: 1.3;
  /* 135% */
}
@media (max-width: 767px) {
  .stats__intro br {
    display: none;
  }
}

.stats__legend {
  margin: 0;
  display: block;
  font-size: 20px;
  line-height: 1;
  /* 100% */
}

.stats__legend--top {
  color: rgba(0, 0, 0, 0.7);
}
@media (max-width: 767px) {
  .stats__legend--top {
    display: none;
  }
}

.stats__legend--bottom {
  text-align: center;
  color: rgba(255, 255, 255, 0.7);
}
@media (min-width: 768px) {
  .stats__legend--bottom {
    display: none;
  }
}

.stats__list {
  margin: 0;
  margin-bottom: 20px;
  background-color: #353535;
}

.stats__item {
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

.stats__item:nth-child(1) {
  background-color: rgba(45, 38, 130, 0.05);
}

.stats__item:nth-child(2) {
  background-color: rgba(255, 255, 255, 0.05);
}

.stats__number {
  display: block;
}